                <h4>{{listData.total}} artikel ditemukan</h4>
              </div>
              <div class="r-select" @click.prevent="showPopUp = !showPopUp" >
                <div class="r-icon-left">
                    <img :src="require('@/assets/filter_list_24px.svg')" alt="">
                </div>
                <p class="r-select-label">Pengaturan</p>
                <div class="r-icon-right">
                    <img :src="require('@/assets/arrow_down.svg')" alt="">
                </div>
                <div class="r-select-popup" v-if="showPopUp" :style="`${showContent === null ? 'right' : 'left'}: 0px`">
                    <div class="r-fixed-transparentbg" ></div>
                    <div class="columns">
                        <div class="column r-has-rightBorder r-select-custom">
                            <p class="r-select-title">Urutkan Berdasarkan</p>
                            <div class="r-radio-button" @click.prevent="sort = 'date'">
                                <img :src="require(`@/assets/toggle/radio_button_24px_outlined_${sort == 'date' ? 'on' : 'off'}.svg`)" alt="">
                                <p>Tanggal Terbaru</p>
                            </div>
                            <div class="r-radio-button" @click.prevent="sort = 'AtoZ'">
                                <img :src="require(`@/assets/toggle/radio_button_24px_outlined_${sort == 'AtoZ' ? 'on' : 'off'}.svg`)" alt="">
                                <p>Judul (A-Z)</p>
                            </div>
                            <div class="r-radio-button" @click.prevent="sort = 'ZtoA'">
                                <img :src="require(`@/assets/toggle/radio_button_24px_outlined_${sort == 'ZtoA' ? 'on' : 'off'}.svg`)" alt="">
                                <p>Judul (Z-A)</p>
                            </div>
                        </div>
                        <div class="column r-select-custom">
                            <p class="r-select-title">Filter Jenis Ketentuan</p>
                            <div class="r-radio-button" @click.prevent="filterType(cat.id)" v-for="cat in categories" :key="`cat-${cat.id}`">
                                <img :src="require(`@/assets/toggle/check_box_24px_outlined_${isFilter(cat.id) ? 'on' : 'off'}.svg`)" alt="">
                                <p>{{cat.name}}</p>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
          </div>
            <r-sidebar @pagination="pagination" :data="listData" :sidebarVersion="sidebarVersion" @setContent="setShowContent" v-if="!$store.state.isAuthenticated"></r-sidebar>
            <v-tabs class="policy-list-tabs" background-color="transparent" v-if="$store.state.isAuthenticated">
                <!-- tabs -->
                <v-tab class="v-tab-policy-list tab-policy-list-1" :href="`#mandatory`" >
                Harus Dipelajari
                </v-tab>
                <v-tab class="v-tab-policy-list tab-policy-list-2" :href="'#others'"  >
                Lainnya
                </v-tab>
                <!-- tabs -->
                <!-- main content -->
                <!-- mandatory -->
                <v-tab-item :value="'mandatory'" :style="showContent !== null ? 'background: #f1f1f1 !important' : ''" >
                  <r-sidebar @pagination="pagination" ref="rsidebarmandatory" :sidebarVersion="sidebarVersion" :data="listData" @setContent="setShowContent"></r-sidebar>
                </v-tab-item>
                <!-- mandatory -->
                <!-- others -->
                <v-tab-item :value="'others'"  :style="showContent !== null ? 'background: #f1f1f1 !important' : ''">
