                        <div class="r-suggestion" v-if="showSuggestion">
                        <div class="r-fixed-transparentbg" style="background: #0000000f;" @click.prevent="showSuggestion = false; showSuccess = false" ></div>
                            <div class="r-upArrow-shape"></div>
                            <div class="r-suggestion-box">
                                <div v-if="!showSuccess">
                                    <h3>Kotak Saran</h3>
                                    <textarea class="r-textarea-field" rows="5" v-model="saran"></textarea>
                                    <div class="r-button r-lightgreen-outline" style="width: 135px" @click="sendSuggest()">Kirim Saran</div>
                                </div>
                                <div v-if="showSuccess">
                                    <h3>Terima Kasih</h3>
                                    <p>Saran berhasil dikirim dan akan kami tindak lanjuti secepatnya</p>
                                    <img style="margin: auto;" :src="require('@/assets/suggestion_success.svg')" alt="">
                                </div>
                            </div>

                        </div>
