<script>
function loadItem  (view) {
  return () => import(/* webpackChunkName: "view-[request]" */ `./${view}.vue`)
}

import apiClient from '@/client/index.js';


export default {
  components: {
    'r-info': loadItem ('info'),
    'r-detail': loadItem ('detail'),
    'r-provision': loadItem ('provision'),
    'r-sidebar': loadItem ('sidebar'),
    'login-slider': () => import(/* webpackChunkName: "loginSlider" */ '@/components/LoginSlider')
  },
  data: () => ({
    sidebarVersion: 1,
    showSuccess: false,
    showSuggestion: false,
    showPopUp: false,
    sort: 'date',
    filterData: [],
    categories: [],
    activeTab: 'info',
    showContent: null,
    selectValue: null,
    listData: {
        current_page: 1,
        data: []
    },
    listDetail:{
        info: {},
        rincian: {},
        ketentuan_terkait: []
    },
    saran: null
  }),
  mounted() {
      this.getCat()
  },
  computed: {
    maxHeight(){
      return window.innerHeight - 310;
    },
    isLogin(){
      return this.$store.state.isLogin
    },
    isMinimize(){
      return this.$store.state.minimizeSideBar
    },
    policyDetailId(){
      return this.$store.state.policyDetailId
    }
  },
  watch: {
    policyDetailId(){
      if(this.policyDetailId !== null && this.policyDetailId != this.showContent){
        this.showContent = this.policyDetailId
      }
    },
    $route(to, from) {
        if (from.params.search !== to.params.search) {
            this.showContent = null
            return this.getData(`?page=1`,to.params.search);
        }
        if (from.params.id !== to.params.id) {
            this.showContent = to.params.id
        }
    },
    sort(){
        this.getData(`?page=${this.listData.current_page}`)
    },
    isLogin(){
      if(this.isLogin){
        this.showContent = null
      }
    },
    isMinimize(){
      if(!this.isMinimize){
        this.showContent = null
      }
    },
    showContent(){
      this.$store.commit('setPolicyDetailId',this.showContent)
      if(this.showContent === null){
        this.sidebarVersion = 1
        this.$store.commit('maximizeSideBar')
      }else{
        this.sidebarVersion = 2
        this.$store.commit('minimizeSideBar')
        this.$store.commit('hideLogin')
      }
        this.getDetail()
    }
  },
  methods: {
      sendSuggest(){
        if(this.saran === null || this.saran === ''){
          alert('Saran tidak boleh kosong')
        }else{
          this.showSuccess = true
          this.saran = null
        }
      },
      setShowContent(source){
        this.showContent = source
      },
      downloadPdf(){
        apiClient.watermarkPdf(this.listDetail.info.link_policy,this.$store.state.userObject.nik)
                  .then(res => {
                      const url = window.URL.createObjectURL(new Blob([res.data]));
                      const link = document.createElement('a');
                      link.href = url;
                      link.setAttribute('download', `${this.listDetail.info.title}.pdf`);
                      document.body.appendChild(link);
                      link.click();
                  })
      },
      getCat(){
          apiClient.getCategory().then(res => {
              this.categories = res.data
              res.data.forEach(row => {
                  this.filterData.push(row.id)
              });
          }).then(() => {
              this.getData(`?page=1`);
          })
      },
      getDetail(){
        if(this.showContent === null){
          return
        }
          apiClient.getPolicyDetail(this.showContent).then(res => {
              this.listDetail = res.data.response_data
          })
      },
      getData(page = '?page=1', search = this.$route.params.search){
          var sortBy = 'created_at'
          var sorting = 'DESC'
          if(this.sort !== 'date'){
              sortBy = 'name'
              if(this.sort === 'AtoZ'){
                  sorting = 'ASC'
              }else{
                  sorting = 'DESC'
              }
          }
          apiClient.getPolicy(page, sortBy, sorting, search, this.filterData)
                      .then(res => {
                          this.listData = res.data.response_data
                      })
      },
      pagination(source){
        this.moveCurPage(source.type,source.number)
      },
      moveCurPage(type, number = null){
        if(type === 'previous' && this.listData.current_page > 1){
            this.getData(`?page=${this.listData.current_page-1}`)
        }else if(type === 'next' && this.listData.current_page < this.listData.last_page){
            this.getData(`?page=${this.listData.current_page+1}`)
        }else if(type === 'first' && this.listData.current_page != 1){
            this.getData('?page=1')
        }else if(type === 'last' && this.listData.current_page != this.listData.last_page){
            this.getData(`?page=${this.listData.last_page}`)
        }else if(number === 'number'){
            this.getData(`?page=${type}`)
        }
      },
      filterType(type){
        if(this.isFilter(type)){
          var temp = this.filterData.filter(row => {
            return row !== type
          })
          this.filterData = temp
        }else{
          this.filterData.push(type)
        }
        this.getData(`?page=1`,this.$route.params.search);
      },
      isFilter(type){
        return this.filterData.filter(row => {
          return row === type
        }).length > 0
      },
  },
}
</script>
